ks-order-columns {
    display: flex;
    gap: 20px;
    padding: 0px 30px;
    margin: 30px 0px;
}

ks-order-columns > * {
    flex: 1 1 auto;
    width: 100%;
}

@media (max-width: 959px) {
    ks-order-columns {
        flex-direction: column;
    }
}

@media (min-width: 1200px) {
    ks-order-columns {
        padding: 0px 40px;
        margin: 40px 0px;
    }
}

@media (max-width: 720px) {
    ks-order-columns {
        padding: 0px 15px;
        margin: 40px 0px;
    }
}

ks-order-columns h1,
ks-order-columns h2 {
    font-size: 20px;
    font-weight: 700;
    font-family: var(--font-emphasis);
}